.tile {
  height: 150px;
  width: 150px;
  border: 1px solid black;
  padding: 5px;
  border-radius: 3px;
  margin: 10px;
  display: inline-block;
  -webkit-box-shadow: 3px 3px 6px rgba(0, 0, 0, 0.4);
          box-shadow: 3px 3px 6px rgba(0, 0, 0, 0.4);
  background-color: #C0FFEE;
}

.tile:hover {
  -webkit-box-shadow: inset 3px 3px 6px rgba(0, 0, 0, 0.3);
          box-shadow: inset 3px 3px 6px rgba(0, 0, 0, 0.3);
  background-color: #abef98;
}

.img-container {
  text-align: center;
}

.img-container > img {
  height: 100px;
}

.content {
  text-align: center;
}


